.excerpt {
  padding: 0 10px 1.5rem 10px;
  position: relative;
  z-index: 1;
  &_meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    transform: translateY(-2.5rem);
    position: relative;
    z-index: 5; } }

.archive {
  &_item {
    display: grid;
    padding: 1.5rem 0; }

  &_title {
    margin: 0; } }

.article {
  box-shadow: 0 0.5rem 2rem rgba(0,0,0,0.12);
  overflow: hidden;
  border-radius: 0.5rem;
  &_title {
    margin: 0; }
  &_excerpt {
    &:not(.visible) {
      height: 0;
      opacity: 0; }
    transition: height 0.5s, opacity 0.5s; }
  &_excerpt,
  &_meta {
    transform-origin: bottom; }
  &_meta {
    padding: 10px 1.25rem 1.25rem;
    color: var(--text);
    position: relative;
    z-index: 2;
    transition: margin-top 0.5s;
    background: var(--bg);
    &.center_y {
      transform-origin: center;
      transition: transform 0.5s;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @media screen and (min-width: 42rem) {
        left: -2rem; } } }

  &_thumb {
    display: grid;
    position: relative;
    z-index: 0;
    overflow: hidden;
    height: 15rem;
    background-size: cover;
    background-position: 50% 50%;
    @media screen and (min-width: 35rem) {
      height: 22.5rem; }

    img {
      transition: transform 0.5s, opacity 0.5s; }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      bottom: 0;
      z-index: 1;
      background: var(--bg);
      opacity: 0;
      transition: opacity 0.1s ease-out; } }

  &_showcase &_thumb {
    height: 15rem; }

  &_showcase &_meta {
    padding-top: 1.5rem; }

  &:hover &_thumb {
    img {
      transform: scale(1.1); }

    &::after {
      transition: opacity 0.1s ease-out;
      opacity: 0.5; } }

  &:hover &_excerpt:not(.visible) {
    height: 75px;
    opacity: 1; }

  &:hover &_meta {
    &:not(.center_y) {
      margin-top: -75px; }

    @media screen and (min-width: 769px) {
      &.center_y {
        transform: translateX(-3rem); } } }

  &:hover {
    box-shadow: 0 1.5rem 6rem rgba(0,0,0,0.17);
    a {
      color: initial !important; } }

  &_hidden {
    display: none; } }
